/* Estilos generales */
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #9acef8; /* Cambia el color de fondo según tus preferencias */
  }
  
  /* Desactivar el zoom y fijar tamaño */
  html, body {
    overflow: hidden;
    max-width: 100%;
    height: 100%;
    touch-action: none;
  }
  
  /* Estilo para el banner superior */
  #banner {
    background-color: #2196f3;
    text-align: center;
    padding: 1px; /* Ajusta el valor según tus necesidades */
    width: 200vw; /* Ocupa todo el ancho de la pantalla */
  }
  
  #banner img {
    max-width: 500px; /* Ajusta el valor para el triple de su tamaño actual */
    height: auto;
    max-height: 150px; /* Ajusta el valor según tus necesidades */
  }
  
  /* Estilo para la imagen en el centro */
  #content {
    text-align: center;
    padding: 20px;
  }
  
  #content img {
    max-width: 90%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
  
  #buttonInicio {
    background-color: #2196f3; /* Cambia el color del botón */
    color: white;
    padding: 15px 20px; 
    border: none;
    cursor: pointer;
    margin-top: 50px; 
  }
  
/* estilo para el gellery*/
/* Agrega estos estilos para la vista de la galería */
#gallery-header {
    background-color: #2196f3;
    text-align: center;
    padding: 10px 20px;
    width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  #logo img {
    max-width: 150px;
    height: auto;
  }
  
  #menu-button {
    background-color: #9acef8;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
  }
  
  #gallery-content {
    text-align: center;
    padding: 20px;
    overflow-y: scroll;
    max-height: calc(100vh - 60px);
  }
  
  #gallery-content img {
    max-width: 90%;
    height: auto;
    display: block;
    margin: 20px auto; /* Espacio entre las imágenes */
  }
